body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
/*  */
.light-mode {
  background-color: #fff;
  color: #333;
  transition: background-color 0.3s ease-out;
}

.light-mode nav {
  transition: background-color 0.3s ease-out;

}

.light-mode div {
  transition: background-color 0.3s ease-out;

}

.dark-mode {
    background-color: #1a1919;
    color: #999;
  transition: background-color 0.3s ease-out;

}

.dark-mode nav {
    background-color: #1b2938;
  transition: background-color 0.3s ease-out;

}
.dark-mode div {
  transition: background-color 0.3s ease-out;

}

div {
  /* margin-bottom: 2rem; */
}

/*  */
nav {
  width: 100%;
  display: flex;
  background-color: steelblue;
  padding: 20px;
}

main {
  padding: 0.5em;
  margin: 0 auto;
  max-width: 600px;
  font-size: 1.2rem;
  line-height: 1.1;
  /* height: 90vh; */
}

h1 {
  margin: 0;
}

.toggle {
  position: relative;
  padding: 0 4px;
  display: flex;
  align-items: center;
}

input[type="checkbox"].checkbox {
  width: 40px;
  height: 10px;
  background: #555;
  position: relative;
  border-radius: 5px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  cursor: pointer;
  vertical-align: 2px;
  outline: none;
}

input[type="checkbox"].checkbox:checked + label {
  left: 30px;
}

input[type="checkbox"].checkbox:focus-visible {
  outline: solid 2px white;
}

input[type="checkbox"].checkbox + label {
  display: inline-block;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  transition: all 0.3s ease;
  cursor: pointer;
  position: absolute;
  left: 2px;
  background: #fff;
  opacity: 0.9;
  background-color: cyan;
}

.toggle-container {
  display: flex;
  margin: 0 auto;
}

.toggle-container > span {
  font-size: 1.2em;
  background: none;
  border: none;
  cursor: pointer;
  transition: color 0.3s ease;
  margin-top: 4px;
  margin-right: 2px;
}

.toggle-container > span:last-child {
  margin-top: 5px;
}


.full {
  height: 100vh;
}